/*
 * Membership Forms Styles
 */

@use "../abstracts" as *;

/* Sign up form */

 #register-form {
    grid-column: 1 / span 6;
    padding: 50px 70px;
    background-color: white;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    box-shadow: 0px 0px 10px rgba(0,0,0,0.5);

    @include max-responsify($device-large) {
        grid-column: 1 / span 12; 
        border-radius: 5px;
    }

    .form-header {
        margin-bottom: 20px !important;
    }

    h2 {
        font-weight: 200;
        margin-bottom: 0.2em;
    }

    input {
        width: 100%;
        border-radius: 5px;
        padding: 0.3rem 1rem;
        border: 2px solid #ced4da;
        transition: 0.5s;

        &:focus-visible {
            border-color: $color-theme1;
        }
    }

    .nice-select {
        width: 100%;
    }
    
    .nice-select .current {
        width: 100%;
        border-radius: 5px;
        padding: 0.4rem 1rem;
        text-transform: capitalize;
        border: 2px solid #ced4da;
    }

    textarea {
        height: 10em;
        resize: none;
        width: 100%;
        padding: 0.4em 1em;
        border: 2px solid #ced4da;
        border-radius: 0;

        &:focus-visible {
            border-color: $color-theme1;
        }
    }
}

/* Password Visibility Button */
.password-visibility-btn {
	position: absolute;
	right: 2px;
	top: 0;
	bottom: 0;
	height: calc(100% - 4px);
	margin: auto;
	border: none;
	display: flex;
	background: white;
    padding: 0 1em;
	border-top-right-radius: 4px;
	border-bottom-right-radius: 4px;

    i {
        margin: auto;
        color: gray;
        font-size: 1em;
    }

    &:hover {
        background: white;
    }

    &:focus {
        border: none;
    }
}

/* Login form */

#login-form {
    grid-column: 8 / span 5;
    padding: 40px 60px;
    border-radius: 10px;
    background-color: white;

    .form-header {
        padding: 0;
        border: none;
        margin-bottom: 20px !important;
    }

    h2 {
        text-align: left;
    }

    input {
        width: 100%;
        display: block;
        border-radius: 5px;
        padding: 0.3rem 1rem 0.3em 2.5em;
        border: 2px solid #ced4da;
        transition: 0.5s;

        &:focus-visible {
            border-color: $color-theme1;
        }
    }

    #forgot-pass {
        color: black;
        display:block;
        float: right;
        margin-bottom: 20px;
    }

    .form-footer {
        margin-top: 20px;
    }
}

/* Form groupings */

.form-grouping {
	margin-bottom: 15px;

    div {
        position: relative;

        > i {
            position: absolute;
            top: 0.8em;
            left: 1.2em;
            color: gray;
            font-size: 0.9em;
        }
    }
}


/* Labels */

#register-form label, #login-form label {
	color: gray;
	display: block;
	margin-bottom: 3px;
	cursor: pointer;
}

/* Form footer */

.form-footer {
	margin-top: 20px;
	margin-bottom: 0px;
	text-align: center;

    a {
        display: block;
    }
}

/* Form messages */
.error {
	margin: 0;
	color: #9a3a43;
	margin-bottom: 0.8em;
	padding: 0.5em 0;
	text-align: center;
	border-radius: 5px;
	background: #f8d7da;
}

/* Button */
.form-btn {
	width: 100%;
}