/*
 * Sign Up page specific styles
 */

@use "../../abstracts" as *;

#register-body {
    background-color: whitesmoke;

    .page-content {
        margin: 25px 0px;
    }
}

/* Form grid */
#register-grid {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
}

#form-thumbnail {
	grid-column: 7 / span 6;
	overflow: hidden;
	display: flex;
	align-items: center;
	justify-content: center;
	border-top-right-radius: 5px;
	border-bottom-right-radius: 5px;
	box-shadow: 0px 0px 10px rgba(0,0,0,0.5);
	background-image: linear-gradient(rgba(0,0,0,0.5),rgba(0,0,0,0.5)), url(/static/images/gallery/img3.jpg);
	background-size: cover;
    background-position-x: 60%;

    @include max-responsify($device-large) {
        display: none;
    }

    > div {
        width: 80%;
        height: 90%;
        color: white;

        h2 {
            font-size: 3em;
        }
    }
}